.App {
  font-family: sans-serif;
}

.first-screen {
  width: 100%;
  /* height: 100vh; */

}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 0.5rem;
}

form {
  margin-bottom: 1rem;
}



.done {
  text-decoration: line-through;
}

.textField {
  margin-bottom: 0.5rem;
  margin-right: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  width: 200px;
  height: 40px;
  font-size: 18px;
  background: #ffffff;
  border: 2px solid #e8e8e8;
  box-sizing: border-box;
  border-radius: 10px;
}

.addTodo {
  width: 60px;
  height: 40px;
  font-size: 16px;
  color: #ffffff;
  background: #2091f9;
  border-radius: 10px;
  align-items: center;
  justify-content: center;
}

.remove {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  background: #e2523e;
  border-radius: 8px;
  font-size: 1.2em;
}

.point {
  cursor: pointer;
}

::-webkit-input-placeholder {
  color: #444;
  font-style: italic;
  padding-right: 1rem;
}

::-moz-placeholder {
  color: #444;
  font-style: italic;
  padding-right: 1rem;
}

:-ms-input-placeholder {
  color: #444;
  font-style: italic;
  padding-right: 1rem;
}

:-moz-placeholder {
  color: #444;
  font-style: italic;
  padding-right: 1rem;
}